$light__color:white;
$dark__color:rgb(29, 29, 29);

* {
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
  }
  
  img {
    width: 50%;
    margin: auto;
  }
  
  .header {
    width: 100%;
    text-align: center;
    padding: 40px 0 40px 0;
    background: white;
    box-shadow: 0px 14px 19px -8px rgba(50, 50, 50, 0.75);
  }
  
  .main-menu {
    width: 50%;
    height: 100% !important;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: 1fr;
    /* grid-row-gap: 30px; */
    grid-column-gap: 10px;
    text-align: center;
    position: relative;
  }
  
  .wide-menu {
    width: 70%;
    grid-template-columns: 3fr 1fr;
  }
  
  .beer__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 30px;
    grid-column-gap: 10%;
  }
  
  .beer {
    text-align: left;
  }
  
  h2 {
    font-size: 40px;
    font-weight: bold;
    font-family: karantina;
  }
  
  h3 {
    font-size: 200%;
    font-weight: 300;
    font-family: karantina;
  }
  
  .cost {
    font-weight: bold;
  }
  
  p {
    font-family: roboto;
  }
  
  .order-btn {
    width: 35px;
    height: 35px;
    transform: translate(-150%, 110%);
    cursor: pointer;
  }
  
  .fake-div {
    background-color: $light__color;
    width: 100px;
    height: 200px;
  }
  
  .cart-main {
    background-color: $dark__color;
    height: 100px;
    width: 100%;
    position: fixed;
    bottom: 0;
  }
  
  .cart-sub-main {
    width: 100%;
    margin: auto;
    text-align: left;
  }
  
  .btn-container {
    text-align: center;
  }
  
  .btn-title {
    font-family: roboto;
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
    color: white;
    margin-top: 12px;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-name: bounce-2;
    animation-timing-function: ease;
  }
  
  .btn {
    margin-top: 10px;
    background-color: #000000;
    border-radius: 30px;
    border: 2px solid #ffffff;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: roboto;
    text-transform: uppercase;
    /* font-size: 100%; */
    font-weight: bold;
    padding: 6px 15px;
    text-decoration: none;
    margin-right: 10px;
  }
  .delete {
    color: rgb(139, 9, 9);
    cursor: pointer;
    font-weight: bold;
  }
  
  @keyframes bounce-2 {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .cart__support {
    position: absolute;
    right: 0;
    top: 20vh;
  }
  
  .cart {
    position: fixed;
    cursor: pointer;
  }
  
  .cart-number {
    position: absolute;
    top: 35%;
    right: 15%;
    font-size: 200%;
    text-align: center;
  }
  
  .cart-comunicat {
    position: absolute;
    right: 0;
    text-align: center;
    font-family: roboto;
    visibility: hidden;
  }
  
  .cart-comunicat-visible {
    visibility: visible;
  }
  
  .cart-img {
    width: 40%;
    height: 40%;
  }
  
  .cart__container {
    text-align: center;
  }
  
  .cart-off {
    visibility: hidden;
  }
  
  .total {
    font-weight: bold;
  }
  .item-name {
    font-weight: lighter;
  }
  .items {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  
  .order__comming {
    width: 0;
    height: 0;
    background-color: white;
    z-index: 100;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    vertical-align: middle;
    overflow: hidden;
  }
  
  .order__comming h2 {
    margin-top: 50vh;
  }
  
  .show__order {
    animation-duration: 2s;
    animation-name: showOrder;
    animation-timing-function: ease;
  }
  
  @keyframes showOrder {
    0% {
      opacity: 0;
      width: 100vw;
      height: 100vh;
    }
    25% {
      opacity: 1;
      width: 100vw;
      height: 100vh;
    }
    50% {
      opacity: 1;
      width: 100vw;
      height: 100vh;
    }
    70% {
      opacity: 1;
      width: 100vw;
      height: 100vh;
    }
    100% {
      opacity: 0;
      width: 100vw;
      height: 100vh;
    }
  }
  
  @media only screen and (max-width: 1100px) {
    .beer__container {
      grid-template-columns: 1fr;
    }
  }
  
  
  